.page-title{
    text-align: center;
    margin-bottom: 3rem;
    font-size: 3.5rem;
}

.page-subtitle{
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    font-size: 2.5rem;
}

.page-caption{
    text-align: center;
    margin-top: 2.5rem;
    margin-bottom: 1.5rem;
    font-size: 1.08rem;
}

.content-card{
    max-width: 15rem;
    flex: none !important;
    min-height: 330px;
}

.card-image-container{
    max-height: 116.05px;
    overflow: hidden;
}

.content-card-text{
    font-size: 0.9rem;
    line-height: 1.05rem;
}

.card-title{
    font-size: 1.2rem;
}

@media (max-width: 574px){
    .content-card{
        max-width: 14rem;
    }
}

@media (max-width: 541px){
    .content-card{
        max-width: 12rem;
        margin: 0.5rem;
    }

    .content-card-text{
        font-size: 0.8rem;
        line-height: 1.05rem;
    }
}

@media (max-width: 450px){
    .content-card{
        max-width: 11rem;
        margin: 0.25rem;
    }

    .content-card-text{
        font-size: 0.8rem;
        line-height: 1.05rem;
    }
}

.content-card-deck{
    display: flex !important;
    flex-flow: row wrap !important;
}